

.tarjeta{
    position: relative;
    width: 600px;
    padding: 20px;
    max-width: 85vw;
    height: 50vh;
    border-radius: 20px;
    background-size: cover;
    background-position: center;
    box-shadow: 0 20px 25px 0 rgba(0, 0, 0, 0.048);
}

.logo{
    width: 50px;
    height: 50px;
    display: block;
}
.colorNav{
    background-color: #AABDBF;
    width: cover;
}

.contenedorTarjeta{
    display: flex;
    justify-content: center;
    margin-top: 5vh;
}

.tarjeta h2{
    position: absolute;
    bottom: 10px;
    color: white;
    
}





.swipe{
    position: absolute;
}
